// src/pages/LoginPage.tsx
import React, { useCallback, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from '../components/atoms/Button';
import { Icon } from '../components/atoms/Icon';
import AuthService from '../services/authService';
import styles from './LoginPage.module.scss';

const LoginPage: React.FC = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [errors, setErrors] = useState<{
    email?: string;
    password?: string;
    general?: string;
  }>({});
  const [touched, setTouched] = useState<{
    email?: boolean;
    password?: boolean;
  }>({});
  const [loginAttempts, setLoginAttempts] = useState(0);
  const [isAccountLocked, setIsAccountLocked] = useState(false);
  const [serverStatus, setServerStatus] = useState<'checking' | 'online' | 'offline'>('checking');
  const navigate = useNavigate();

  // Check authentication status on mount
  useEffect(() => {
    const checkAuthStatus = async () => {
      try {
        // Check if user is already authenticated
        if (AuthService.isAuthenticated() && !AuthService.isSessionExpired()) {
          const user = AuthService.getCurrentUser();
          if (user) {
            navigate('/dashboard');
            return;
          }
        }

        // Check server status
        const healthCheck = await AuthService.healthCheck();
        setServerStatus(healthCheck.status === 'healthy' ? 'online' : 'offline');
      } catch {
        setServerStatus('offline');
      }
    };

    // Check local lockout status
    const attempts = parseInt(localStorage.getItem('loginAttempts') || '0');
    const lockoutTime = localStorage.getItem('lockoutTime');

    if (lockoutTime && Date.now() < parseInt(lockoutTime)) {
      setIsAccountLocked(true);
      setLoginAttempts(attempts);
    } else {
      // Clear lockout if time has passed
      localStorage.removeItem('lockoutTime');
      localStorage.removeItem('loginAttempts');
    }

    checkAuthStatus();
  }, [navigate]);

  // Real-time validation
  const validateField = useCallback(
    (field: 'email' | 'password', value: string) => {
      const newErrors = { ...errors };

      if (field === 'email') {
        if (!value) {
          newErrors.email = 'Email l bt buc';
        } else if (!/\S+@\S+\.\S+/.test(value)) {
          newErrors.email = 'Email khng hp l';
        } else {
          delete newErrors.email;
        }
      }

      if (field === 'password') {
        if (!value) {
          newErrors.password = 'Mt khu l bt buc';
        } else if (value.length < 6) {
          newErrors.password = 'Mt khu phi c t nht 6 k t';
        } else {
          delete newErrors.password;
        }
      }

      setErrors(newErrors);
    },
    [errors]
  );

  const validateForm = () => {
    const newErrors: { email?: string; password?: string; general?: string } = {};

    if (!email) {
      newErrors.email = 'Email l bt buc';
    } else if (!/\S+@\S+\.\S+/.test(email)) {
      newErrors.email = 'Email khng hp l';
    }

    if (!password) {
      newErrors.password = 'Mt khu l bt buc';
    } else if (password.length < 6) {
      newErrors.password = 'Mt khu phi c t nht 6 k t';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleEmailChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const value = e.target.value;
      setEmail(value);

      if (touched.email) {
        validateField('email', value);
      }
    },
    [touched.email, validateField]
  );

  const handlePasswordChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const value = e.target.value;
      setPassword(value);

      if (touched.password) {
        validateField('password', value);
      }
    },
    [touched.password, validateField]
  );

  const handleFieldBlur = useCallback(
    (field: 'email' | 'password') => {
      setTouched((prev) => ({ ...prev, [field]: true }));

      if (field === 'email') {
        validateField('email', email);
      } else {
        validateField('password', password);
      }
    },
    [email, password, validateField]
  );

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Tm thi disable account lockout check
    // if (isAccountLocked) {
    //   setErrors({
    //     general: "Ti khon  b kha tm thi. Vui lng th li sau 15 pht.",
    //   });
    //   return;
    // }

    if (!validateForm()) {
      return;
    }

    setIsLoading(true);
    setErrors({});

    try {
      // Call authentication service
      const result = await AuthService.login(email, password, rememberMe);

      if (result.success && result.user) {
        // Successful login
        // Clear login attempts
        localStorage.removeItem('loginAttempts');
        localStorage.removeItem('lockoutTime');

        // Role-based navigation - all users go to dashboard for now
        navigate('/dashboard');
      } else {
        // Failed login
        const newAttempts = loginAttempts + 1;
        setLoginAttempts(newAttempts);
        localStorage.setItem('loginAttempts', newAttempts.toString());

        // Tm thi disable lockout logic  test
        setErrors({
          general: result.error || 'Thng tin ng nhp khng chnh xc.',
        });
      }
    } catch (error) {
      console.error('Login error:', error);
      setErrors({ general: 'C li xy ra. Vui lng th li sau.' });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className={styles.loginPageWrapper}>
      {/* Left Sidebar with Company Info */}
      <div className={styles.leftSidebar}>
        <div className={styles.companyBranding}>
          <div className={styles.brandLogo}>
            <h1> MIA.vn</h1>
            <p>Qun l kinh doanh thng minh</p>
          </div>

          <div className={styles.featuresList}>
            <div className={styles.feature}>
              <div className={styles.featureIcon}></div>
              <div className={styles.featureContent}>
                <h3>Dashboard Thng Minh</h3>
                <p>Theo di hiu sut kinh doanh real-time</p>
              </div>
            </div>

            <div className={styles.feature}>
              <div className={styles.featureIcon}></div>
              <div className={styles.featureContent}>
                <h3>T ng Ha</h3>
                <p>Gim thiu cng vic th cng</p>
              </div>
            </div>

            <div className={styles.feature}>
              <div className={styles.featureIcon}></div>
              <div className={styles.featureContent}>
                <h3>Bo Co Chi Tit</h3>
                <p>Phn tch su d liu kinh doanh</p>
              </div>
            </div>
          </div>

          <div className={styles.companyTagline}>
            <p>"n gin ha qun l, ti u ha li nhun"</p>
          </div>
        </div>
      </div>

      {/* Right Side - Login Form */}
      <div className={styles.loginCard}>
        <div className={styles.loginPage}>
          <div className={styles.header}>
            <div className={styles.logo}>
              <h1> MIA.vn</h1>
            </div>
            <h2>Cho mng tr li!</h2>
            <p>ng nhp  tip tc qun l doanh nghip ca bn</p>

            {/* Server Status Indicator */}
            <div className={`${styles.serverStatus} ${styles[serverStatus]}`}>
              {serverStatus === 'checking' && (
                <>
                  <Icon name="settings" size={12} color="#856404" />
                  <span>ang kim tra kt ni...</span>
                </>
              )}
              {serverStatus === 'online' && (
                <>
                  <Icon name="view" size={12} color="#155724" />
                  <span>Kt ni server thnh cng</span>
                </>
              )}
              {serverStatus === 'offline' && (
                <>
                  <Icon name="close" size={12} color="#721c24" />
                  <span>Khng th kt ni server</span>
                </>
              )}
            </div>
          </div>

          {/* General Error Message */}
          {errors.general && (
            <div className={styles.generalError}>
              <Icon name="close" size={16} color="#dc3545" />
              <span>{errors.general}</span>
            </div>
          )}

          {/* Account Locked Warning */}
          {isAccountLocked && (
            <div className={styles.lockoutWarning}>
              <Icon name="settings" size={16} color="#856404" />
              <span>Ti khon tm thi b kha v qu nhiu ln ng nhp tht bi</span>
            </div>
          )}

          <form onSubmit={handleSubmit} className={styles.form} noValidate>
            <div className={styles.formGroup}>
              <label htmlFor="email">
                <Icon name="people" size={16} color="#666" />
                Email
              </label>
              <input
                id="email"
                type="email"
                value={email}
                onChange={handleEmailChange}
                onBlur={() => handleFieldBlur('email')}
                placeholder="Nhp email ca bn"
                required
                autoComplete="email"
                className={errors.email ? styles.error : ''}
                disabled={isLoading || isAccountLocked}
                aria-describedby={errors.email ? 'email-error' : undefined}
              />
              {errors.email && (
                <span id="email-error" className={styles.errorText} role="alert">
                  {errors.email}
                </span>
              )}
            </div>

            <div className={styles.formGroup}>
              <label htmlFor="password">
                <Icon name="settings" size={16} color="#666" />
                Mt khu
              </label>
              <div className={styles.passwordContainer}>
                <input
                  id="password"
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={handlePasswordChange}
                  onBlur={() => handleFieldBlur('password')}
                  placeholder="Nhp mt khu"
                  required
                  autoComplete="current-password"
                  className={errors.password ? styles.error : ''}
                  disabled={isLoading || isAccountLocked}
                  aria-describedby={errors.password ? 'password-error' : undefined}
                />
                <button
                  type="button"
                  className={styles.passwordToggle}
                  onClick={() => setShowPassword(!showPassword)}
                  title={showPassword ? 'n mt khu' : 'Hin mt khu'}
                  aria-label={showPassword ? 'n mt khu' : 'Hin mt khu'}
                  disabled={isLoading || isAccountLocked}
                >
                  <Icon name={showPassword ? 'close' : 'view'} size={16} color="#999" />
                </button>
              </div>
              {errors.password && (
                <span id="password-error" className={styles.errorText} role="alert">
                  {errors.password}
                </span>
              )}
            </div>

            <div className={styles.formOptions}>
              <label className={styles.checkbox}>
                <input
                  type="checkbox"
                  checked={rememberMe}
                  onChange={(e) => setRememberMe(e.target.checked)}
                  disabled={isLoading || isAccountLocked}
                />
                <span>Ghi nh ng nhp</span>
              </label>
              <button
                type="button"
                className={styles.forgotPassword}
                disabled={isLoading || isAccountLocked}
              >
                Qun mt khu?
              </button>
            </div>

            <div className={styles.actionButtons}>
              <Button
                type="submit"
                variant="primary"
                size="lg"
                loading={isLoading}
                className={styles.submitButton}
                disabled={isLoading || isAccountLocked}
                onClick={() => {}}
              >
                {isLoading ? 'ang ng nhp...' : 'ng Nhp'}
              </Button>
            </div>

            <div className={styles.footer}>
              <p>
                Cha c ti khon?{' '}
                <button type="button" className={styles.signupLink} disabled={isLoading}>
                  ng k ngay
                </button>
              </p>
              <p className={styles.securityNote}> Bo mt vi m ha SSL 256-bit</p>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
