import googleConfig from "../config/googleConfig";

class AuthService {
  constructor() {
    this.baseUrl = googleConfig.api_base_url;
    this.currentUser = null;
    this.sessionTimeout = 24 * 60 * 60 * 1000; // 24 hours
  }

  // Health check endpoint
  async healthCheck() {
    try {
      const response = await fetch(`${this.baseUrl}/health`);
      const data = await response.json();
      return data;
    } catch (error) {
      console.error("Health check failed:", error);
      throw new Error("Server connection failed");
    }
  }

  // ng nhp vi Google Sheets lm database
  async login(email, password, rememberMe = false) {
    try {
      const response = await fetch(`${this.baseUrl}/auth/login`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email,
          password,
        }),
      });

      const data = await response.json();

      if (data.success && data.user) {
        // Lu thng tin user vo localStorage/sessionStorage
        const storage = rememberMe ? localStorage : sessionStorage;
        
        const loginData = {
          user: data.user,
          token: data.token || this.generateSessionToken(),
          loginTime: Date.now(),
          rememberMe: rememberMe
        };

        storage.setItem("authData", JSON.stringify(loginData));
        this.currentUser = data.user;

        return {
          success: true,
          user: data.user,
          message: "ng nhp thnh cng"
        };
      } else {
        return {
          success: false,
          error: data.error || "Thng tin ng nhp khng chnh xc"
        };
      }
    } catch (error) {
      console.error("Login error:", error);
      return {
        success: false,
        error: "Khng th kt ni ti server. Vui lng th li."
      };
    }
  }

  // ng xut
  logout() {
    localStorage.removeItem("authData");
    sessionStorage.removeItem("authData");
    this.currentUser = null;
    
    // Clear login attempts
    localStorage.removeItem("loginAttempts");
    localStorage.removeItem("lockoutTime");
  }

  // Kim tra trng thi ng nhp
  isAuthenticated() {
    const authData = this.getAuthData();
    
    if (!authData) {
      return false;
    }

    // Kim tra session timeout
    if (this.isSessionExpired()) {
      this.logout();
      return false;
    }

    return true;
  }

  // Kim tra session  ht hn cha
  isSessionExpired() {
    const authData = this.getAuthData();
    
    if (!authData) {
      return true;
    }

    const { loginTime, rememberMe } = authData;
    const sessionDuration = rememberMe ? 7 * 24 * 60 * 60 * 1000 : this.sessionTimeout; // 7 days if remember me
    
    return Date.now() - loginTime > sessionDuration;
  }

  // Ly thng tin user hin ti
  getCurrentUser() {
    if (this.currentUser) {
      return this.currentUser;
    }

    const authData = this.getAuthData();
    if (authData && !this.isSessionExpired()) {
      this.currentUser = authData.user;
      return this.currentUser;
    }

    return null;
  }

  // Ly auth data t storage
  getAuthData() {
    // u tin localStorage trc, sau  sessionStorage
    let authData = localStorage.getItem("authData");
    if (!authData) {
      authData = sessionStorage.getItem("authData");
    }

    if (authData) {
      try {
        return JSON.parse(authData);
      } catch (error) {
        console.error("Error parsing auth data:", error);
        return null;
      }
    }

    return null;
  }

  // To session token n gin
  generateSessionToken() {
    return Math.random().toString(36).substring(2) + Date.now().toString(36);
  }

  // Kim tra quyn truy cp (role-based)
  hasPermission(permission) {
    const user = this.getCurrentUser();
    if (!user) {
      return false;
    }

    // Kim tra role v permissions
    const userRole = user.role || 'user';
    const permissions = user.permissions || [];

    // Admin c tt c quyn
    if (userRole === 'admin') {
      return true;
    }

    // Kim tra specific permission
    return permissions.includes(permission);
  }

  // Refresh session (extend session time)
  refreshSession() {
    const authData = this.getAuthData();
    if (authData) {
      authData.loginTime = Date.now();
      const storage = authData.rememberMe ? localStorage : sessionStorage;
      storage.setItem("authData", JSON.stringify(authData));
    }
  }
}

// Export single instance
const authService = new AuthService();
export default authService;
